/* Slideshow container */
.slideshow_container {
  position: relative;
  width: 100%;
  height: 45vh;
  margin: auto;
}

/* Hide the images by default */
.mySlides {
  display: none;
  height: 100%;
  grid-template-rows: 100%;
}

.slide_wrapper{
    grid-row: 1/-1;
    grid-column: 1/-1;
    height: 100%;
    width: 100%;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.6);
}

.slide_text{
    /* padding: 1rem 2rem; */
    /* margin-top: 8rem; */
    font-weight: 700;
    grid-row: 1/-1;
    grid-column: 1/-1;
    z-index: 10;
    align-self: center;
    display: grid;
    grid-gap: 1.5rem;
    justify-self: center;
    width: 75%;
}

.slide_text > h2{
