    background-repeat: no-repeat;
    grid-row: 1/-1;
    grid-column: 1/-1;
}

/* Next & previous buttons */
.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: 58px;
  height: 58px;
  border-radius: 50%;
  margin-top: -22px;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  -webkit-user-select: none;
          user-select: none;
  background: #bab7b3;
  display: grid;
  align-items: center;
  justify-items: center;
  z-index: 5;
}

.next_icon{
    color: white !important;
    font-size: 2.5rem !important;

}

/* Position the "next button" to the right */
.next {
  right: 3rem;
  /* border-radius: 3px 0 0 3px; */
}

.prev {
    left: 3rem;
    /* border-radius: 3px 0 0 3px; */
}

